.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
body {
  background-color: #e1e2e1;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.activeLink {
  color: #fff !important;
}
.search-results {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100vh;
  width: 100%;
  background-color: #ccc;
  z-index: 100;
  padding: 4rem 0;
  /* transform: translateY(25%);
  transition: all 0.3s ease;
  opacity: 0;
  display: none; */
}
.found {
  opacity: 1;
  transform: translateY(0);
  display: block;
}
.navigation-wrap {
  z-index: 1001;
  position: relative;
}
